import axios from 'axios';

// Instan Axios pentru utilizatori
const usersApi = axios.create({
  baseURL: 'https://66ccbdb3a4dd3c8a71b8aaad.mockapi.io/users',
});

// Instan Axios pentru contacte
const contactsApi = axios.create({
  baseURL: 'https://66c3433cd057009ee9bfa42b.mockapi.io/contacts',
});

// Funcie pentru nregistrarea unui utilizator nou
export const registerUser = async (userData) => {
  try {
    // Verificm dac utilizatorul exist deja
    const { data: existingUsers } = await usersApi.get('', {
      params: { email: userData.email }
    });

    if (existingUsers.length > 0) {
      throw new Error('Email already exists. Please use a different email.');
    }

    // Dac nu exist, crem utilizatorul
    const { data } = await usersApi.post('', userData);
    return data;
  } catch (error) {
    console.error('Error registering user:', error);
    throw error;
  }
};

// Funcie pentru autentificarea unui utilizator existent
export const loginUser = async (userData) => {
  try {
    // Verificm utilizatorul n baza de date MockAPI
    const { data } = await usersApi.get('', {
      params: {
        email: userData.email,
        password: userData.password,
      },
    });

    if (data.length > 0) {
      return data[0]; // Returnm utilizatorul gsit
    } else {
      throw new Error('Invalid credentials');
    }
  } catch (error) {
    console.error('Error logging in:', error);
    throw error;
  }
};

// Funcie pentru obinerea contactelor utilizatorului
export const fetchContacts = async () => {
  try {
    const { data } = await contactsApi.get(''); // Folosim instana pentru contacte
    return data;
  } catch (error) {
    console.error('Error fetching contacts', error);
    throw error;
  }
};

// Funcie pentru adugarea unui contact nou
export const addContact = async (contactData) => {
  try {
    const { data } = await contactsApi.post('', contactData); // Folosim instana pentru contacte
    return data;
  } catch (error) {
    console.error('Error adding contact:', error);
    throw error;
  }
};

// Funcie pentru tergerea unui contact
export const deleteContact = async (contactId) => {
  try {
    const { data } = await contactsApi.delete(`/${contactId}`); // Folosim instana pentru contacte
    return data;
  } catch (error) {
    console.error('Error deleting contact:', error);
    throw error;
  }
};
